<script>
import { useI18n } from "../../i18nPlugin";

export default {
    setup() {
        const i18n = useI18n();
        return {
            i18n
        };
    },
    data() {
        return {
            path: "",
            mobile: !this.$store.state.viewListen
        };
    },
    inject: ["reload"],
    methods: {
        switchLanguage(params) {
            console.log(params);
            window.localStorage.setItem("language", params);
            this.i18n.locale.value = params;
        },
        handleSelect(key) {
            this.path = key;
            window.localStorage.setItem("path", key);
        },
        show() {
            if (!document.getElementsByClassName("biu")[0]) {
                let e = document.getElementsByClassName("menu")[0];
                e.classList.remove("overflow");
                e.classList.add("biu");
            } else {
                let e = document.getElementsByClassName("biu")[0];
                e.classList.remove("biu");
                setTimeout(() => {
                    e.classList.add("overflow");
                }, 1000);
            }
        },
        push(path, i) {
            console.log(path);
            window.localStorage.setItem("path", path);
            if (i == 0 || i == 1 || i == 2 || i == 3) {
                console.log(path + "?id=" + i, "biubi");
                this.$router.push(path + "?id=" + i);
            } else {
                this.$router.push(path);
            }
        }
    },
    mounted() {
        this.path =
            window.localStorage.getItem("path") == null
                ? "/home"
                : window.localStorage.getItem("path");
        console.log(this.$store.state.viewListen, "盒子");
        this.screenWidth = document.body.clientWidth;
        // 因为写的时候是先做的网页端，再做的移动端适配导致一部分内容不好调整css，因此用的v-if加载
        // 于是延伸出了一个问题就是当监听页面变化后需要手动刷新一次页面，重新获取列表数据，才能正常切换双端。
        window.onresize = () => {
            return (() => {
                this.screenWidth = document.body.clientWidth;
                if (this.screenWidth <= 600) {
                    this.$store.commit("switchMobile");
                    if (
                        this.$store.state.oldListen !=
                        this.$store.state.viewListen
                    ) {
                        this.$store.commit("cover");
                        this.reload();
                    }
                } else {
                    this.$store.commit("switchPc");
                    if (
                        this.$store.state.oldListen !=
                        this.$store.state.viewListen
                    ) {
                        this.$store.commit("cover");
                        this.reload();
                    }
                }
            })();
        };
    },
    watch: {
        $route: {
            handler: function(val) {
                this.handleSelect(val.href);
            }
        }
    }
};
</script>
